//🍅Сюда импортируем все из этой папки
@import './logo.scss';
@import './theme-switcher';
.header {
  height: 230px;
  @media screen and (min-width: 320px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
     url(../images/header/header_m_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/header_m_x2.jpg);
    }
    .s_form_input {
      display: inline-flex;
      width: 99%;
    }
    .search_pic {
      margin-left: -22px;
    }
  }
  @media screen and (min-width: 768px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)), 
    url(../images/header/header_t_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/header_t_x2.jpg);
    }
    .search_form{
      display: flex;
      align-items: center;
      justify-content: center;  
    }
    .s_form_input {
      width: 50%;
    }
  }
  @media screen and (min-width: 1024px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
     url(../images/header/header_d_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/header_d_x2.jpg);
    }
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.nav_list {
  display: flex;
  align-items: baseline;
  justify-content: space-around;
  margin-left: 39px;
  margin-top: 58px;
  margin-bottom: 41px;
}
.nav_link {
  color: var(--p-dark-t-color);
  text-align: right;
  &.current {
    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 3px;
      background-color: var(--accent-color);
      transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
  &:hover,:focus{
    color: var(--accent-color);
    font-style: italic;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
}
.nav_link:first-of-type {
  margin-right: 39px;
}
.search_btn {
  position: absolute;
  background-color: transparent;
  border: none;
  color: var(--p-dark-t-color);
}
.s_form_input {
  color: var(--p-dark-t-color);
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--p-dark-t-color);
  &::placeholder {
    font-size: var(--font-regular);
    color: var(--p-dark-t-color);
    border-bottom: 1px solid var(--p-dark-t-color);
    margin-bottom: 4px;
    border: none;
  }
  &:focus,
  :hover {
    border: none;
    outline: none;
    border-bottom: 1px solid var(--p-dark-t-color);
  }
}
.site_nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.error_text {
  display: none;
  color: var(--p-error-t-color);
  text-align: center;
  margin-top: 22px;
}
.btn_div{
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
.lib_btn{
  font-size: var(--font-size-main);
  height: 44px;
  background-color: transparent;
  color: var(--p-dark-t-color);
  border: 1px solid var(--p-dark-t-color);
  border-radius: 3px;
  &:first-of-type{
    margin-right: 7%;
  }
  @media screen and (min-width: 320px) {
  width: 45%;}
  @media screen and (min-width: 768px){
    width: 136px;
  }
  &:hover,:focus{
    color: var(--p-text-color);
    background-color: #fff;
    font-style: italic;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
  }
}
.current_btn{
  background-color: var(--accent-color);
  border: transparent;
}

.library{
  @media screen and (min-width: 320px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
     url(../images/header/lib_m_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/lib_m_x2.jpg);
    }
  }
  @media screen and (min-width: 768px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
     url(../images/header/lib_t_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/lib_t_x2.jpg);
    }
  }
  @media screen and (min-width: 1024px) {
    background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
     url(../images/header/lib_d_x1.jpg);
    @media screen and (min-resolution: 2dppx) {
      background-image:linear-gradient(rgba(47, 48, 58, 0.4),rgba(47, 48, 58, 0.4)),
       url(../images/header/lib_d_x2.jpg);
    }}
background-repeat: no-repeat;
background-size: cover;
}